import React from 'react';

const Dashboard = React.lazy(() => import('../pages/Dashboard'));
const Anuncios = React.lazy(() => import('../pages/Anuncios'));
const Anuncio = React.lazy(() => import('../pages/Anuncio'));
const AnuncioDetails = React.lazy(() => import('../pages/AnuncioDetails'));
const AnuncioVideoPhotos = React.lazy(() => import('../pages/AnuncioVideoPhotos'));
const Animais = React.lazy(() => import('../pages/Animals'));
const Profile = React.lazy(() => import('../pages/Profile'));

const routes = [
  {
    path: '/',
    name: 'Dashboard',
    component: Dashboard,
  },
  {
    path: '/:token',
    name: 'Dashboard',
    component: Dashboard,
  },
  {
    path: '/perfil',
    name: 'Profile',
    component: Profile,
  },
  {
    path: '/anuncios',
    name: 'Anuncios',
    component: Anuncios,
  },
  {
    path: '/anuncio',
    name: 'Anuncio',
    component: Anuncio,
  },
  {
    path: '/anuncio-detalhes',
    name: 'AnuncioDetails',
    component: AnuncioDetails,
  },
  {
    path: '/anuncio-video-fotos/:id',
    name: 'AnuncioVideoPhotos',
    component: AnuncioVideoPhotos,
  },
  {
    path: '/animais',
    name: 'Animais',
    component: Animais,
  },
];

export default routes;
