/* eslint-disable no-nested-ternary */
import React, { useCallback, forwardRef, useImperativeHandle, useContext } from 'react';
import { isMobile } from 'react-device-detect';
import { NavLink } from 'react-router-dom';
import { Fade } from 'reactstrap';

import { AppContext } from '../../contexts/app';

import './styles.css';

function Sidebar({ children, header, items = [] }, ref) {
  const { sidebarShow, toggleSidebar, minimizeSidebar } = useContext(
    AppContext,
  );

  const open = useCallback(() => {
    toggleSidebar();
  }, []);

  const close = useCallback(() => {
    toggleSidebar();
  }, []);

  const minimize = useCallback(() => {
    minimizeSidebar();
  }, []);

  /* const SubItem = ({ subItem: { to, icon, name } }) => (
    <Fade in={sidebarWidth > 0}>
      {to ? (
        <NavLink to={to} activeClassName="active" className="subitem">
          <i className={`${icon} ${sidebarWidth === 60 ? 'minimized' : ''}`} />
          {' '}
          <Fade in={sidebarWidth > 60} className={icon ? 'ml-3' : ''}>
            <span>{name}</span>
          </Fade>
        </NavLink>
      ) : (
        <div className="item subitem">
          <i className={`${icon} ${sidebarWidth === 60 ? 'minimized' : ''}`} />
          {' '}
          <Fade in={sidebarWidth > 60} className={icon ? 'ml-3' : ''}>
            <span>{name}</span>
          </Fade>
        </div>
      )}
    </Fade>
  ); */

  useImperativeHandle(ref, () => ({
    open,
    close,
    minimize,
  }));

  return (
    <div
      id="sidebar"
      className="sidebar opacity-8 opacity-sm-10"
      style={{
        width: `${sidebarShow === 'minimized' ? 60 : sidebarShow === 'opened' ? 250 : 0}px`,
      }}
    >
      <span
        role="button"
        tabIndex="0"
        className="btn-close close d-sm-none"
        onClick={close}
        onKeyPress={close}
      >
        <i className="fas fa-times" />
      </span>
      <span
        role="button"
        tabIndex="0"
        className="btn-minimize close"
        onClick={sidebarShow === 'minimized' ? () => open() : () => minimize()}
        onKeyPress={sidebarShow === 'minimized' ? () => open() : () => minimize()}
      >
        <i
          className="fas fa-arrow-left"
          style={{
            transition: '1s',
            transform: sidebarShow === 'minimized' ? 'scaleX(-1)' : 'scaleX(1)',
          }}
        />
      </span>
      <div
        className={`header ${sidebarShow === 'minimized' ? 'minimized' : ''}`}
      >
        {header || (sidebarShow === 'minimized' ? (
          <i className="fas fa-user-cog" />
        ) : (
          <>
            <i className="fas fa-user-cog" />
            <Fade in={!(sidebarShow === 'minimized')}>
              <strong className="font-weight-800">ADMIN</strong>
            </Fade>
          </>
        )
        )}
      </div>
      {children}
      {items.map(({ to, icon, name, exact }, key) => (
        <Fade key={key} in={sidebarShow === 'opened' || sidebarShow === 'minimized'}>
          {to ? (
            <NavLink
              to={to}
              className="item-link"
              activeClassName="active"
              end={exact || false}
              onClick={() => {
                if (isMobile) {
                  close();
                }
              }}
            >
              <i className={`${icon} ${sidebarShow === 'minimized' ? 'minimized' : ''}`} />
              <Fade in={!(sidebarShow === 'minimized')} className={icon ? 'ml-3' : ''}>
                <span>{name}</span>
              </Fade>
            </NavLink>
          ) : (
            <div className="item" key={key}>
              <i className={`${icon} ${sidebarShow === 'minimized' ? 'minimized' : ''}`} />
              {' '}
              <Fade in={!(sidebarShow === 'minimized')} className={icon ? 'ml-3' : ''}>
                <span>{name}</span>
              </Fade>
            </div>
          )}
        </Fade>
      ))}
    </div>
  );
}

export default forwardRef(Sidebar);
